import { Button } from '@mui/material'
import * as React from 'react'
import { useNavigate } from 'react-router-dom'
import useStyles from './connect.style'

const ConnectSection = () => {
  const classes = useStyles()
  const navigate = useNavigate()

  const handleClick = () => {
    navigate('/sign-in')
  }

  return (
    <section className={classes.connect}>
      <div className={classes.connect__container}>
        <article className={classes.connect__article}>
          <h2 className={classes.connect__header}>Подключение к Программе Трудоустройства</h2>
          <p className={classes.connect__text}>
            В Карьерном трекере ты сможешь поучаствовать в самых эффективных форматах работы со студентами в Практикуме.
            Откликайся на партнёрские или рыночные вакансии, работай с реальными карьерными экспертами, делись опытом с
            другими студентами и оффер не заставит себя ждать
          </p>
          <Button className={classes.submitButton} type="submit" style={{ width: 250 }} onClick={handleClick}>
            Подключайте меня
          </Button>
        </article>
      </div>
    </section>
  )
}

export default ConnectSection
